export const searchMixin = {
  data() {
    return {
      query: '',
      refreshDelay: 120
    }
  },
  computed: {
    // ...mapGetters([
    //   'searchHistory'
    // ])
  },
  methods: {
    onQueryChange(query) {
      this.query = query
    },
    blurInput() {
      this.$refs.searchBox.blur()
    },
    addQuery(query) {
      this.$refs.searchBox.setQuery(query)
    },
    saveSearch() {
      this.updateHostorList(this.query)
    },
    ...mapActions([
      'updateHostorList',
      'deleteSearchHistory'
    ])
  }
}

